.eltdf-page-header,
.eltdf-top-bar,
.eltdf-sticky-header,
.eltdf-mobile-header {
    .widget {
        display: inline-block;
        vertical-align: middle;
        margin: 0;

        // prevent monster widget from displaying in our header widget areas
        &.widget_pages,
        &.widget_archive,
        &.widget_categories,
        &.widget_meta,
        &.widget_recent_entries,
        &.widget_recent_comments,
        &.widget_rss,
        &.widget_calendar,
        &.widget_rss,
        &.widget_archive,
        &.widget_categories,
        &.widget_tag_cloud,
        &.widget_nav_menu,
        &.widget_search {
            display: none !important;
        }
    }
}
.eltdf-mobile-header {
    div[id*="monster"]{
        display: none!important;
    }
}