/* ==========================================================================
   Default WordPress widgets styles - begin
   ========================================================================== */

.widget {
    margin: 0 0 $widget-bottom-margin;
    padding: 0;

    .eltdf-widget-title-holder {
        margin-bottom: $widget-list-item-padding;

        .eltdf-widget-title {
            margin-bottom: 7px;
        }

        .eltdf-separator {
            border-bottom-width: 3px;
            border-color: $widget-text-color;
            width: 30px;
        }
    }

    ul {
        list-style: none;
        margin: 0;
        padding: 0;

        li {
            display: block;
            font-size: 11px;
            color: $widget-link-color;
            line-height: $widget-list-item-height;
            margin: 0;
            font-weight: 400;

            &:last-child {
                border: 0;
                padding-bottom: 0;
            }

            a {
                color: $widget-link-color;

                &:hover {
                    color: $widget-link-hover-color;
                }
            }
        }

        .children,
        .sub-menu {
            padding-left: 10px;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with lists and animation on link hover

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_entries,
    &.widget_nav_menu {
        .eltdf-widget-title {
            margin-bottom: 0;
        }

        ul {
            li {
                overflow: hidden;

                a {
                    display: inline;
                    overflow: hidden;
                    padding: 0;
                    position: relative;
                    color: $widget-link-color;
                    font-family: $default-text-font;
                    font-size: 11px;
                    font-weight: 400;
                    line-height: 25px;
                    letter-spacing: 0.3em;
                    text-transform: uppercase;

                    @include eltdfTransition(padding .2s ease);
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with lists and w/o animation on link hover

    &.widget_recent_comments,
    &.widget_rss {
        .eltdf-widget-title {
            margin-bottom: 0;
        }
        ul{
            li{
                font-size: inherit;
                font-weight: inherit;
                line-height: inherit;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // calendar

    #wp-calendar {
        text-align: center;
        width: 100%;

        caption {
            margin: 0 0 $widget-list-item-padding;

        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            background-color: $default-heading-color;
            color: #FFF;
        }

        tfoot a {
            color: $widget-link-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // rss

    &.widget_rss {

        .rss-widget-icon {
            display: inline-block;
        }

        .rss-date {
            display: block;
            margin-bottom: $widget-list-item-padding;
        }

        cite {
            display: block;
            margin-top: calc(#{$widget-list-item-padding} / 2);
        }
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // search

    .eltdf-searchform {
        .input-holder {
            border-bottom: $widget-list-item-border-width $widget-list-item-border-style $widget-list-item-border-color;

            input,
            button {
                background-color: transparent;
                border: 0;
                border-radius: 0;
                box-sizing: border-box;
                color: $first-main-color;
                display: inline-block;
                float: left;
                font-family: $default-text-font;
                font-size: 11px;
                font-weight: 400;
                letter-spacing: 0.3em;
                text-transform: uppercase;
                height: $widget-search-form-height;
                line-height: calc(#{$widget-search-form-height} - 2px);
                outline: 0;
                position: relative;
                vertical-align: top;
                -webkit-appearance: none;

                @include eltdfTransition(color .2s ease-in-out);
            }

            input.search-field {
                width: calc(100% - 25px);

                @include eltdfPlaceholder {
                    color: $widget-text-color;
                }
            }

            button {
                cursor: pointer;
                font-size: 16px;
                width: 25px;

                @include eltdfTransition(color .2s ease-in-out);

                &:hover {
                    color: $widget-link-hover-color;
                }

                span {
                    display: block;
                    left: 0;
                    line-height: inherit;
                    position: absolute;
                    top: 0;
                    width: 100%;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // combobox

    &.widget_archive,
    &.widget_categories,
    &.widget_text {
        select {
            background-color: transparent;
            border-color: $widget-list-item-border-color;
            border-radius: 0;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 15px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            margin-top: $widget-list-item-padding;
            outline: 0;
            padding: 0 0 0 21px;
            position: relative;
            vertical-align: top;
            width: 100%;

            @include eltdfTransition(color .2s ease-in-out);

            option {
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // tag cloud

    .tagcloud {
        a {
            display: inline-block;
            font-size: 11px !important;
            font-family: $default-text-font;
            font-weight: 400;
            letter-spacing: .3em;
            text-transform: uppercase;
            

            &:hover {
                color: $widget-link-hover-color;
            }

            &:after{
                content: " - ";
            }
            &:last-child{
                &:after{
                    content: "";
                }
            }

        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // text

    &.widget_text {
        img {
            display: block;
        }

        p {
            &:first-child {
                margin-top: 0;
                padding-top: $widget-list-item-padding;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}
.page-template-blog-centered{
    .widget{
        display: none;
    }
}