@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.eltdf-comment-holder {
    @include eltdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .eltdf-comment-holder-inner {
        padding: 87px 0 20px;
    }

    .eltdf-comment {

        &.eltdf-pingback-comment {

            .eltdf-comment-text {
                padding: 0;
            }
        }
    }

    .eltdf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include eltdfRelativeHolderLayout();
            padding: 28px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 111px;
        }
    }

    .eltdf-comments-title {
        margin-bottom: 24px;

        > * {
            margin: 0;
        }
    }

    .eltdf-comment-image {
        position: relative;
        display: block;
        top: 6px;
        left: 0;
        width: 80px;
        height: 80px;
        float: left;

        img {
            display: block;
        }
    }

    .eltdf-comment-text {
        padding: 0 0 0 110px;
        min-height: 80px;

        .eltdf-comment-info {
            @include eltdfRelativeHolderLayout();
            margin: 11px 0 2px;
        }

        .eltdf-text-holder{
            p{
                margin: 8px 0 0;
            }
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: right;
            margin-left: 10px;
            font-size: 11px;
            color: #353535;
            font-weight: 400;
            letter-spacing: 0.3em;
            text-transform: uppercase;

            &:hover {
                color: $default-heading-color;
            }
        }

        .eltdf-comment-name {
            float: left;
            margin: 0;
            text-transform: capitalize;
        }

        .eltdf-comment-date {
            float: left;
            margin: 0;
            font-size: 15px;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }

			p.form-submit{
				text-align: right;
				margin-top: 39px;
			}
        }

        #cancel-comment-reply-link {
			float: right;
            font-size: 11px;
            color: #353535;
            font-weight: 400;
			font-family: $default-text-font;
            letter-spacing: 0.3em;
            text-transform: uppercase;
			margin-left: 10px;

            &:hover {
                color: $default-heading-color;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
    &.eltdf-post-comment-alignment-centered{
        .eltdf-comments-title{
            text-align: center;
        }
    }
}

.eltdf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;

    .eltdf-comment-form-inner {
        padding: 59px 0 40px;

        .comment-form-cookies-consent{
            display: inline-block;
            margin: 10px 0 0;
        }
        .form-submit{
            text-align: right;
            margin: 39px 0 0;
        }
    }

    .comment-reply-title {
        margin: 0 0 16px;
    }

    .logged-in-as {
        margin: 0 0 8px;
    }

    &.eltdf-post-comment-alignment-centered{
        .comment-reply-title{
            text-align: center;
        }
    }
}

@include ipad-portrait {

    .eltdf-comment-holder {

        .eltdf-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .eltdf-comment-holder {

        .eltdf-comment-list {

            .children {
                padding: 0;
            }
        }

        .eltdf-comment-text {

            .eltdf-comment-date,
            .eltdf-comment-name {
                float: none;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {

    .eltdf-comment-holder {

        .eltdf-comment-image {
            float: none;
        }

        .eltdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.eltdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
    border: none;
    border-bottom: 1px solid $default-heading-color;
    padding: 15px 5px 16px;
}
#respond textarea{
   height: 80px;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;
        border: 1px solid transparent;
        border-bottom: 1px solid $default-border-color;

        footer & {
            margin: 0;
        }
    }
    &.wpcf7-textarea {
        height: 118px;
        margin: 0 0 53px;
    }
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
    @include eltdfButtonDefaultStyle();
    @include eltdfButtonSize(large);
    @include eltdfButtonSolidColor();

    cursor: pointer;

    &:hover {
        @include eltdfButtonSolidHoverColor();
    }
}

.eltdf-right-button {
    input.wpcf7-form-control.wpcf7-submit {
        float: right;
    }

}

p.form-submit,
.wpcf7-form p {
    margin: 0;
}

#respond textarea,
.eltdf-style-form textarea,
.wpcf7-form textarea {
    display: block;
}

.eltdf-two-columns-form-without-space {
    .wpcf7-form .ajax-loader {
        display: none;
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Newsletter styles - start
   ========================================================================== */
   .eltdf-ft-newsletter {

    position: relative;
   

    .eltdf-column-ft-right {

        display: table-cell;
        width: 6%;
        text-align: left;
        vertical-align: middle;
        border-bottom: 1px solid #808080;

        @include ipad-portrait {
            display: block;
            border-bottom: 1px solid transparent;
            margin-top: 10px;

            .eltdf-ft-after {

                &:after{
                   
                    right: -75px;
                   
                    }

                    &:hover{
                        &:after{
                            right:-45px;
                        }
                    }

              }


        }
    }

    .eltdf-column-ft-left {

        display: table-cell;
        width: 36%;
        vertical-align: middle;
        border-bottom: 1px solid #808080;

    }

    input.wpcf7-form-control.wpcf7-submit {
        padding: 0!important;
        color: $header-light-color;
        background-color: transparent;
        border: 0;
        vertical-align: middle;
        font-size: 11px;

        &:hover {
            color: $header-light-color;

        }

    }
     .eltdf-ft-after {

        line-height: 0;
        position: relative;

        &:after{
            content: "";
            position: absolute;
            right: 0;
            top: 14px;
            display: inline-block;
            background-color: $header-light-color;
            height: 1px;
            width: 33px;
            @include eltdfTransition(right .4s ease);
            }

            &:hover{
                &:after{
                    right: 28px;
                }
            }

      }
     .wpcf7-form-control.wpcf7-text {
        border-bottom: 1px solid transparent;
        color: #808080;
        text-transform: capitalize;
        letter-spacing: 0;
        padding: 12px 0;
        font-size: 14px;
        &:focus {
            color: #fff;
        }
     }

   }

/* ==========================================================================
   #Newsletter end - start
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.eltdf-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

	.eltdf-pag-name{
		display: none; //to hide pagination names in items
	}

    &.eltdf-slider-is-moving {
        .owl-item {
            .eltdf-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.eltdf-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 34px); // 34 is is half height of pagination control + top margin
                @include eltdfTransform(translateY($pos));
            }
        }
    }

    &.eltdf-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 60px;
            height: 60px;
            padding: 0;
            margin: 0;
            background: none;
            background-color: #FFF;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;
            @include eltdfTransition(color .2s);

            > span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-size: 16px;
                color: $default-heading-color;
                line-height: 1;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
            
            &:hover {
                > span {
                    color: #6a6a6a;
                }
            }
        }
        .owl-prev{
            bottom: 61px;
        }
    }

    .owl-dots {
        text-align: center;
        margin: 39px 0 0;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;
            padding: 0;
            margin: 0 6px;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;

            span:not(.eltdf-owl-pag-name) {
                display: inline-block;
                vertical-align: middle;
                width: 6px;
                height: 6px;
                border-radius: 3em;
                background-color: #999;
                box-sizing: border-box;

                @include eltdfTransition(background-color .2s ease-out, border .2s ease-out);
            }

			.eltdf-owl-pag-name{
                font-family: $default-text-font;
                font-size: 11px;
                line-height: 2.8em;
                letter-spacing: 0.3em;
                text-transform: uppercase;
                position: relative;
				line-height: initial;

				&:after{
					content: '';
                    position: absolute;
                    top: calc(50% - 1px);
                    left: calc(100% - 12px);
                    width: 20px;
                    height: 1px;
                    background-color: currentColor;
                    opacity: 0;
                    @include eltdfTransform(translateX(16px));
                    @include eltdfTransitionTransform(.4s cubic-bezier(0.13, 0.68, 0.05, 0.83),opacity .1s);
				}
            }
            
            &:hover,
            &.active {
                span:not(.eltdf-owl-pag-name) {
                    background-color: $default-heading-color;
                }

				.eltdf-owl-pag-name:after{
					opacity: 1;
					@include eltdfTransform(translateX(0));
				}
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .eltdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .eltdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                @include eltdfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.eltdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .eltdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

.eltdf-owl-slider[data-enable-pagination-names=yes] {

	.owl-dots {

		.owl-dot {
			margin: 0 28px;
		}
	}
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {
    .eltdf-wrapper {
        padding: 0 !important;
    }

    .eltdf-content {
        height: 100vh;
    }

    &.admin-bar {
        .eltdf-content {
            height: calc(100vh - 32px);
        }
    }

    .eltdf-content-inner {
        @include eltdfTableLayout();
    }

    .eltdf-page-not-found {
        @include eltdfTableCellLayout();

        padding: 0 20%;
        text-align: center;

        .eltdf-404-title {
            margin: 0;
        }

        .eltdf-404-subtitle {
            margin: 35px 0 0;
        }

        .eltdf-404-text {
            margin: 10px 0 0;
        }

        .eltdf-404-form{
            &.eltdf-404-form-light{
                .input-holder{
                    border-color: $header-light-color;

                    input,
                    button{
                        color: $header-light-color;
                    }

                    input.search-field {

                        
                    }

                    button:hover{
                        color: rgba(255,255,255,0.8);
                    }
                }
            }

            .input-holder {
                width: 45%;
                min-width: 250px;
                border-bottom: 1px solid #000;
                margin: 30px auto 0;

                input,
                button {
                background-color: transparent;
                border: 0;
                border-radius: 0;
                box-sizing: border-box;
                color: $first-main-color;
                display: inline-block;
                float: left;
                font-family: $default-text-font;
                font-size: 11px;
                font-weight: 400;
                letter-spacing: 0.3em;
                text-transform: uppercase;
                height: 46px;
                line-height: calc(46px - 2px);
                outline: 0;
                position: relative;
                vertical-align: top;
                -webkit-appearance: none;

                    @include eltdfTransition(color .2s ease-in-out);
                }

                input.search-field {
                    width: calc(100% - 48px);
                    padding: 10px 0 10px 21px;

                   
                }

                button {
                    background: transparent;
                    color: $default-heading-color;
                    cursor: pointer;
                    font-size: 17px;
                    line-height: 1em;
                    width: 48px;

                    @include eltdfTransition(color .2s ease-in-out);

                    &:hover {
                        color: #000;
                    }

                    span {
                        display: block;
                        left: 0;
                        line-height: 48px;
                        position: absolute;
                        top: 0;
                        width: 100%;

                        &:before {
                            display: block;
                            line-height: inherit;
                        }
                    }
                }
            }
        }
    }

    @include ipad-landscape {
        .eltdf-content {
            height: 100%;
            min-height: 100vh;
        }

        &.admin-bar {
            .eltdf-content {
                height: 100%;
                min-height: calc(100vh - 32px);
            }
        }
    }

    @include ipad-portrait {
        .eltdf-page-not-found {
            padding: 70px 30px 20px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#eltdf-back-to-top {
    position: fixed;
    width: 41px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;

        @include eltdfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;

        @include eltdfTransition(opacity 0.3s ease);
    }

    > span {
        position: relative;
        display: block;
        width: 39px;
        height: 39px;
        line-height: 39px;
        font-size: 15px;
        text-align: center;
        color: $header-light-color;
        background-color: $first-main-color;
        border: 1px solid $first-main-color;
        overflow: hidden;

        @include eltdfTransition(color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out);

        i {
            @include eltdfTransition(.15s ease .05s);
        }

        &:hover {
            background-color: #FFF;
            border: 1px solid $default-heading-color;
            color: $default-heading-color;

            i {
                @include eltdfTransform(translateY(-8px));
                @include eltdfTransition(.2s cubic-bezier(1, -1.21, 0.25, 1));
            }
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }

    @include phone-landscape{
        bottom: 96px;
    }
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

