/*
==========================================================================
   #Custom classes start
   ==========================================================================
*/

.eltdf-link-hover-class{
    a{
        position: relative;
        background-image: none !important; //override underline
        
        &:after{
            content: '';
            position: absolute;
            top: calc(50% - 1px);
            left: calc(100% - 28px);
            width: 50px;
            height: 1px;
            background-color: currentColor;
            opacity: 0;
            @include eltdfTransform(translateX(16px));
            @include eltdfTransitionTransform(.4s cubic-bezier(0.13, 0.68, 0.05, 0.83),opacity .1s);
        }

        &:hover{
            &:after{
                @include eltdfTransform(translateX(0));
                opacity: 1;
            }
        }
    }
}

.eltdf-content-fade-in .eltdf-eh-item-content > div {
    visibility: hidden;

    .touch & {
        visibility: visible;
    }

    .no-touch & {
        visibility: visible;
        opacity: 0;

        &.eltdf-in-view {
            @include eltdfTransition(opacity 1s);
            opacity: 1;
        }
    }
}

.wpb_text_column a {
    @include eltdfUnderline($speed: .4s);
}

