.eltdf-side-menu {
    // widgets spacing
    $widget-bottom-margin:0;
    $widget-bottom-padding: 15px;
    $widget-inner-padding: 15px;

    // widget color
    $widget-background-color: transparent;

    // widget border
    $widget-border-color: $default-border-color;
    $widget-border-width: 1px;
    $widget-border-style: solid;

    // list items dimensions
    $widget-list-item-height: 20px;
    $widget-list-item-padding: 15px;

    // list items color
    $widget-list-item-background-color: #999999;

    // list items border
    $widget-list-item-border-color: #cccccc;
    $widget-list-item-border-width: 1px;
    $widget-list-item-border-style: solid;

    // text
    $widget-text-color: #fff;
    $widget-heading-color: #999999;

    // links
    $widget-link-color: $widget-text-color;
    $widget-link-hover-color: #666666;

    // specific widgets
    $widget-search-form-height: $input-height;
    $widget-tag-cloud-height: 30px;

    .eltdf-instagram-feed {

        
        &.eltdf-instagram-gallery.eltdf-col-1 {

            li {

                width: 100%;

            }
        }
        &.eltdf-instagram-carousel {
            li {

                a {

                    .eltdf-instagram-icon {
                        background-color: #1c1c1c;
                        width: 56px;
                        height: 53px;
                    }
                }
            }

        }
        li {

            a {

                .eltdf-instagram-icon {
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 56px;
                    height: 53px;
                    color: #fff;
                    z-index: 1;
                    width: 100%;
                    height: 100%;

                }
            }
        }
    }


    .eltdf-widget-title-holder {

        .eltdf-widget-title {

            color: $widget-text-color;
            text-align: center;
        }
    }
    .widget.widget_archive,
    .eltdf-side-menu .widget.widget_categories,
    .eltdf-side-menu .widget.widget_text {

        select {

            color: #fff;

        }
    }
    .widget {
        #wp-calendar {
            td#today {
                color: $first-main-color;
                background-color: #fff;
            }
        } 
    }
    #wp-calendar {
       color: $widget-text-color;
       
        td#today {
            background-color: $widget-text-color;
            color: $first-main-color;
        }

        tfoot a {
            color: $widget-text-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
        tbody,
        thead {
            tr {
                border: 1px solid $widget-text-color;
            }
        } 
        
    }

    .widget {

        .eltdf-searchform {
            .input-holder {
                button,
                input,
                input::placeholder {

                    color: $widget-text-color;

                }
            }
        }


        &.widget_archive,
        &.widget_categories,
        &.widget_text {
            select {
         
                color: $widget-text-color;
               

               
                option {
                    color: $first-main-color;
                }
            }
        }

        &.widget_pages,
        &.widget_archive,
        &.widget_categories,
        &.widget_meta,
        &.widget_recent_entries,
        &.widget_nav_menu {
            

            ul {
                li {

                    a {
                        color: $widget-text-color;
                        text-align: center;
                        &:hover {
                            color: $widget-link-hover-color;
                        }
                    }
                }
            }
        }

        ul {

            li {
                text-align: center;
                color: $widget-text-color;

                a,
                span,
                div  {
                    color: $widget-text-color;
                }
            }
        }

        p {
            color: $widget-text-color;
        }

    }
    
   

    @import 'widgets/default-wordpress-widgets-flat';
    @import 'widgets/standalone-widgets';
}